.info-name{
    font-size: 20px;
    font-weight: bold;
    color: #ffab00;
}

.info-flag{
    height: 80px;
    width: 100%;
    background-size: cover;

}

.info-confirmed,
.info-recovered,
.info-deaths{
    font-size: 16px;
    margin-top: 5px;
}

.info-container{
    width:150px;
}